                if(this.billData[i].bill_category == 'credit' && this.billData[i].bill_item != 'Room Clearance'){
                    this.advanceCalculated += parseInt(this.billData[i].cost)
                }
                if(this.billData[i].bill_category == 'credit' && this.billData[i].bill_item == 'Room Clearance'){
                    this.roomClearanceCalculated += parseInt(this.billData[i].cost)
                }
                
            }

